/*
Dark Shade #1f2041
Purple #bc9cff
Green #6fccf97
*/
body {
  margin: 0;
  padding: 0;
}
::placeholder{ 
  font-family: Open Sans;
  font-style: normal;
}
h1 {
  font-family: Open Sans;
  font-style: normal;
  font-weight: bold;
  font-size: 24px;
  line-height: 30px;
  margin: 0;
  margin-bottom: 20px;
  color: #1f2041;
  user-select: none;
}

h2 {
  font-family: Open Sans;
  font-style: normal;
  font-weight: bold;
  font-size: 19px;
  line-height: 24px;
  user-select: none;
  color: #1f2041;
}

h3 {
  margin: 0;
  margin-top: 20px;
  user-select: none;
  font-family: Open Sans;
  font-style: normal;
  font-weight: bold;
  font-size: 12px;
  line-height: 15px;

  text-transform: uppercase;

  color: #1f2041;
}

body {
  margin: 0;
  padding: 0;

  font-family: Open Sans;
  font-style: normal;
  font-weight: normal;
  font-size: 14px;
  line-height: 24px;

  color: rgba(31, 32, 65, 0.75);
}
